








import { Component, Prop, Vue } from 'vue-property-decorator';
import AppButton from './button.vue';

@Component({
  components: { AppButton }
})
export default class SearchInput extends Vue {
  @Prop() list: any[];
  @Prop() value: string;

  onChange(evt) {
    this.$emit('input', evt.target.value);
  }

  onClear() {
    this.$emit('input', '');
  }
}

